/**
 * Combine all reducers in this file and export the combined reducers.
 */

import { combineReducers } from 'redux'
import { connectRouter } from 'connected-react-router'

import history from 'utils/history'
import storage from 'redux-persist/lib/storage' // defaults to localStorage for web
import { persistReducer } from 'redux-persist'
import marketPageReducer from 'views/Market/reducer'
import globalReducer from 'views/App/reducer'
import operatsPageReducer from 'views/History/reducer'
import modalReducer from 'views/Modal/reducer'
import userReducer from 'views/User/reducer'
import operatReducer from 'views/Create/reducer'
import usersPageReducer from 'views/Users/reducer'
import chatReducer from 'views/Chat/reducer'
import subscriptionsReducer from 'views/Subscriptions/reducer'
/**
  * Merges the main reducer with the router state and dynamically injected reducers
  */
export default function createReducer (injectedReducers = {}) {
  const globalPersistConfig = {
    key: 'global',
    storage: storage
  }

  const userPersistConfig = {
    key: 'user',
    storage: storage
  }
  const rootReducer = combineReducers({
    router: connectRouter(history),
    ...injectedReducers,
    marketPage: marketPageReducer,
    operatsPage: operatsPageReducer,
    global: persistReducer(globalPersistConfig, globalReducer),
    modal: modalReducer,
    user: persistReducer(userPersistConfig, userReducer),
    operat: operatReducer,
    usersPage: usersPageReducer,
    chat: chatReducer,
    subscriptions: subscriptionsReducer
  })

  const persistConfig = {
    key: 'root',
    storage,
    blacklist: ['global', 'user']
  }
  return persistReducer(persistConfig, rootReducer)
}
