const createRow = (style) => (
  <Row>
    <Col sm='12' md={{ size: 3, offset: 4 }}>
      <Spinner color='primary' style={style} animation='border' variant='info'>
        {' '}
      </Spinner>
    </Col>
  </Row>
)

const createModalBody = () => (
  <ModalBody>{createRow({ width: '8rem', height: '8rem' })}</ModalBody>
)

const createModalBodyWithFooter = (dispatch) => (
  <ModalBody>
    {' '}
    {createRow({ width: '6rem', height: '6rem' })}
    {createModalFooter(dispatch)}
  </ModalBody>
)

const createModalFooter = (dispatch, cancel = false) => (
  <ModalFooter>
    <Button
      color='info'
      onClick={() => {
        dispatch(hideLoadingScreen())
      }}
    >
      {cancel ? 'anuluj' : 'ok'}
    </Button>
  </ModalFooter>
)

