function OperatList () {
  const data = useSelector(state => state.modal.data)
  const operatPageState = useSelector(state => state.operatsPage)
  const reduxDispatch = useDispatch()
  const currentOperat = data.operat
  const operats = operatPageState.operats.operats.filter(
    x =>
      x.operatTypeId === currentOperat.operatTypeId && x.id !== currentOperat.id
  )
  const listGroup = (
    <ListGroup style={{ overflow: 'scroll', maxHeight: '67vh' }}>
      {operats.map((operat) => (
        <ListGroupItem
          key={operat.id}
          action
          onClick={async () => {
            const result = await confirm({
              title: 'Ostrzeżenie',
              message:
                'Czy na pewno chcesz skopiować dane z tego operatu? Dane zostaną nadpisane',
              confirmText: 'Kopiuj',
              cancelText: 'Wróć',
              confirmColor: 'success',
              cancelColor: 'link text-primary'
            })
            if (result) {
              const handleResponse = () => {
                reduxDispatch(hideLoadingScreen())
                getFormatedFilesByOperat(reduxDispatch, currentOperat)
              }
              reduxDispatch(
                showLoadingScreen(modalTypeConstants.LOADING, {
                  message: 'Trwa kopiowanie danych z operatu'
                })
              )
              switch (data.dataType) {
                case 'params':
                  operatFilesService
                    .copyOperatParams(operat.id, currentOperat.id)
                    .then(() => {
                      handleResponse()
                    })
                  break
                case 'csv':
                  operatFilesService
                    .copyOperatCsv(operat.id, currentOperat.id)
                    .then(() => {
                      handleResponse()
                    })
                  break
                case 'photos':
                  operatFilesService
                    .copyOperatPhotos(operat.id, currentOperat.id)
                    .then(() => {
                      handleResponse()
                    })
                  break
                default:
              }
            }
          }}
          tag='a'
        >
          {operat.name}
        </ListGroupItem>
      ))}
    </ListGroup>
  )
  return (
    <>
      <ModalHeader>Wybierz operat, z którego chcesz skopiować dane</ModalHeader>
      <ModalBody>
        {operats.length > 0
          ? listGroup
          : 'Brak operatów, z których można by skopiować dane'}
      </ModalBody>
      {createModalFooter(reduxDispatch, true)}
    </>
  )
}

