function CreateSubscriptionForm () {
  const dispatch = useDispatch()
  const [maxNrOfOperats, setMaxNumberOfOperats] = useState(20)
  const [maxNrOfGenerations, setMaxNrOfGenerations] = useState(200)
  const operator = useSelector(state => state.subscriptions.currentOperator)

  async function submitCreateSubscriptionForm () {
    const subscription = {
      id: operator.id,
      maxNrOfOperats,
      maxNrOfGenerations
    }

    dispatch(createSubscription(subscription))
  }

  return (
    <>
      <ModalHeader>Utwórz subskrypcję</ModalHeader>
      <Form
        id='create-subscription-form'
        onSubmit={(e) => {
          e.preventDefault()
          submitCreateSubscriptionForm()
        }}
      >
        <ModalBody>
          <Row>
            <Col>
              <h5>Operator</h5>
            </Col>
          </Row>
          <Row>
            <Col>
              <p>
                <b>Id:&nbsp;</b>
                {operator.id}
              </p>
              <p>
                <b>Nazwa:&nbsp;</b>
                {operator.name}
              </p>
            </Col>
          </Row>
          <Row>
            <Col>
              <h5>Ustawienia subskrypcji</h5>
            </Col>
          </Row>
          <Row>
            <Col>
              <Label for='subscription-max-nr-of-operats'>
                <b>Limit tworzenia operatów:</b>
              </Label>
              <Input
                id='subscription-max-nr-of-operats'
                type='number'
                step='1'
                min='1'
                value={maxNrOfOperats}
                onChange={e => {
                  setMaxNumberOfOperats(parseInt(e.target.value))
                }}
              />
            </Col>
            <Col>
              <Label for='subscription-max-nr-of-generations'>
                <b>Limit tworzenia operatów:</b>
              </Label>
              <Input
                id='subscription-max-nr-of-generations'
                type='number'
                step='1'
                min='1'
                value={maxNrOfGenerations}
                onChange={e => {
                  setMaxNrOfGenerations(parseInt(e.target.value))
                }}
              />
            </Col>
          </Row>
        </ModalBody>
        <ModalFooter>
          <Button color='info' type='submit'>
            Utwórz
          </Button>
          <Button
            color='danger'
            onClick={() => {
              dispatch(hideLoadingScreen())
            }}
          >
            Anuluj
          </Button>
        </ModalFooter>
      </Form>
    </>
  )
}

